.modal {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1050;
  width: 100%;
  height: 100%;
  overflow: hidden;
  outline: 0;
}

.modal-backdrop.show {
  opacity: .5;
}

.modal-backdrop{
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1040;
  width: 100vw;
  height: 100vh;
  background-color: #000000;

}

.fade{
  transition: opacity .15s linear;
}

.modal-dialog{
  position: relative;
  width: auto;
  max-width: 500px;
  margin:1.75rem auto;
  pointer-events: none;
}

.modal-dialog-centered{
  display: flex;
  align-items: center;
  min-height: calc(100% - 3.5rem) ;
}

.modal-content{
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
  pointer-events: auto;
  background-color: #ffffff;
  background-clip: padding-box;
  border: 1px solid rgba(0,0,0,.2);
  border-radius: .3rem;
  outline: 0;

}

.modal-body{
  position: relative;
  flex:1 1 auto;
  padding: 1rem;
}

.form-row{
  padding: 20px 0px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-right: -5px;
  margin-left: -5px;
}

.col{
  flex-basis: 0;
  flex-grow: 1;
  max-width: 100%;
}

.form-row > .col{
  padding-right: 5px;
  padding-left: 5px;

}